import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Skeleton from "@mui/material/Skeleton";

const dispatchedEvents = [];

const SrsMuiSkeleton = function (props, ref) {
  return <Skeleton {...props} theme={props.muiTheme}/>;
};

const styles = `
  /* Custom styles for the MuiSkeleton component */
`;

const props = [
  {
    name: "animation",
    defaultValue: "pulse",
    type: "string",
  },
  {
    name: "variant",
    defaultValue: "rectangular",
    type: "string",
  },
  {
    name: "width",
    defaultValue: null,
    type: "number",
  },
  {
    name: "height",
    defaultValue: null,
    type: "number",
  },
  {
    name: "classes",
    defaultValue: null,
    type: "object",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
  // Add other props specific to Skeleton if needed
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSkeleton),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
